import React from 'react';
import "../css/Introduce.css";
import HTMLReactParser from 'html-react-parser';

const Introduces = [
    {
        title: "LCH S HNH THNH",
        description: [
            "Thnh lp thng 2 nm 2024.",
            "<b>AE T 5</b> c thnh lp nhm mc ch <b>hc tp, nghin cu, trao i, h tr ln nhau</b> gia cc thnh vin.",
            "Ni cu mang nhng con ngi bt hnh b x hi t bn rung b.",
            'L mt b phn ca <b title="Tp th lp 12.11 thuc trng THPT Hunh Vn Ngh, nm hc 2023-2024">tp th lp 12.11 (2023-2024 HVN)</b>.'
        ]
    },
    {
        title: "MC TIU HNG N",
        description: [
            "Cc thnh vin thuc <b>AE T 5</b> lun ra sc hc hi tm ti pht huy sc mnh tp th.",
            "Hng n mc tiu cng bng, bnh ng, <b>lt  t bn</b>.",
            "Yu T quc, yu ng bo, t ho dn tc, t tin chin thng."
        ]
    }
]

function Introduce(){
    return(
        <div id="Introduce">
            <h1 id="page-title"><i className="fa-regular fa-star"></i>  INTRODUCING GROUP  <i className="fa-regular fa-star"></i></h1>
            <div id="body">
            {Introduces.map(({title, description}) => (
                <div key={"IntroduceElements-"+title} className="body-element">
                    <h2 className="element-title">{title}</h2>
                    <div className="element-description">
                    {description.map((text, index) => (<p key={`IntroduceElementChildrens-${index}`} className="element-description__line">-{HTMLReactParser(text)}</p>))}
                    </div>
                </div>
            ))}
            </div>    
        </div>
    )
}

export default Introduce;