import React from 'react';
import { useState, useEffect } from 'react';
import "../css/Activities.css";
import { setSelectionRange } from '@testing-library/user-event/dist/utils';

const statusKeys = {
    0: "ang thc hin",
    1: "ang ln k hoch",
    2: "ang Tr hon",
    3: " hon thnh",
    4: " hy"
}

function ShowActivity({act, count}){
    return(
        <tr>
            <td>{count}</td>
            <td>{act.name}</td>
            <td>{act.content}</td>
            <td>{act.creator}</td>
            <td>{act.date}</td>
            <td className={"status"+act.status}>{statusKeys[act.status]}</td>
        </tr>
    )
}

function ShowActivities({activities}){
    const TableFields=[
        "STT", "Tn hot ng", "Ni dung hot ng", "Ngi to", "Ngy to", "Trng thi"
    ]
    return(
        <div id="container-show">
            <div id="searchForm">
                <form>
                    <i className="fa-solid fa-magnifying-glass"></i>
                    <input placeholder='Tm kim hot ng' id="searchActivities"></input>
                </form>
                <div id="searchForm__result"></div>
            </div>
            <table>
                <thead>
                    <tr>
                        {TableFields.map((field, index)=>(<th key={index}>{field}</th>))}
                    </tr>
                </thead>
                <tbody>
                    {activities.map((e, index) => (<ShowActivity key={index} act={e} count={index}></ShowActivity>))}
                </tbody>
                <tfoot>
                    <tr>
                        <td colSpan="6">Tng s hot ng: {activities.length}</td>
                    </tr>
                </tfoot>
            </table>
        </div>
    )
}

function UpdateActivities(){
    const description=[
        {
            icon: "fa-star",
            text: "Hy la chn mt trong cc mc sau y  thit lp hnh thc thao tc vi d liu."
        },
        {
            icon: "fa-star",
            text: "Cc hnh thc c h tr: Thm, Xa, Chnh Sa."
        },
        {
            icon: "fa-heart",
            text: "Chc bn c thi gian tri nghim vui v!"
        }
    ]

    const optionBtns=[
        {
            id: "update-add",
            description: "Thm"
        },
        {
            id: "update-change",
            description: "Chnh Sa"
        },
        {
            id: "update-remove",
            description: "Xa"
        }
    ]
    return(
        <div id="container-update">
            <div id="update-block" className="box_shadow">
                <div id="updateOptions" className="update-block__part">
                    <div id="update-description">
                        <h3>LA CHN HNH THC <i className="fa-solid fa-wand-magic-sparkles"></i></h3>
                        {description.map(({icon, text}, index) => <p key={index}><i className={"fa-solid "+icon}></i> {text}</p>)}
                    </div>
                    {optionBtns.map(({id, description}, index)=><button key={index} id={id} className='active'>{description}</button>)}
                </div>
                <div id="updateForm" className="update-block__part">
                    <form id="update-addform">
                        <label className="updateForm__label" htmlFor='inputName'>
                            <span>Hot ng</span>
                            <input placeholder='Tn hot ng' id="inputName"></input>
                        </label>
                        
                        <label className="updateForm__label" htmlFor='inputContent'>
                            <span>Ni dung</span>
                            <input placeholder="Ni dung" id="inputContent"></input>
                        </label>
                        
                        <label className="updateForm__label" htmlFor='inputCreator'>
                            <span>Ngi ng</span>
                            <input placeholder='Tn ngi ng' id="inputCreator"></input>
                        </label>
                        
                        <label className="updateForm__label" htmlFor='inputDate'>
                            <span>Ngy ng</span>
                            <input type="date" id="inputDate"></input>
                        </label>
                        
                        <label className="updateForm__label" htmlFor='inputStatus'>
                            <span>Trng thi</span>
                            <select name="inputStatus" id="inputStatus">
                                <option value="dangthuchien">ang thc hin</option>
                                <option value="danglenkehoach">ang ln k hoch</option>
                                <option value="dangtrihoan">ang tr hon</option>
                                <option value="dahoanthanh"> hon thnh</option>
                                <option value="dahuy"> hy</option>
                            </select>
                        </label>
                        <button type="submit" className="active">SUBMIT</button>
                    </form>
                </div>
            </div>
        </div>
    )
}

function Activities({activities}){
    const [option, setOption] = useState("showBtn");
    const handleChangeOption = (id) => {
        setOption(id);
    }

    return(
        <div id="Activities">
            <div id="Header">
                <h1>HOT NG CA GROUP</h1>
                <div id="Header__option">
                    <button id="showBtn" className="active" onClick={()=>handleChangeOption("showBtn")} style={{ opacity: option === "showBtn" ? 1 : 0.5}}>Xem</button>
                    <button id="updateBtn" className="active" onClick={()=>handleChangeOption("updateBtn")} style={{ opacity: option === "updateBtn" ? 1 : 0.5}}>Cp nht</button>
                </div>
            </div>
            <div id="container">
                <ShowActivities activities={activities}></ShowActivities>
                <UpdateActivities></UpdateActivities>
            </div>
        </div>
    )
}

export default Activities;